import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import mokupTransactions from "../../mockup/transaction.json"
import useExcelExport from '../../hooks/useExcelExport';

const ONEDAY_IN_MILLISECONDS = 86400000;

const Admin = () => {
  const [transactions, setTransactions] = useState(mokupTransactions);
  const [searchKeyword, setSearchKeyword] = useState("");
  const [dateRange, setDateRange] = useState("");
  const { exportToExcel } = useExcelExport();

  const handleSubmitForm = (event) => {
    event.preventDefault();
    setSearchKeyword(event.target.search.value);
    setDateRange(event.target.dateRange.value);
  };

  const handleSearch = (event) => {
    const { value } = event.target;
    setSearchKeyword(value);
  };

  const handleFillter = (event) => {
    const { value } = event.target;
    setDateRange(value);
  };

  const handleExportExcel = () => {
    exportToExcel(transactions, "transactions.xlsx");
  }

  useEffect(() => {
    let filteredTransactions = mokupTransactions;
    if (searchKeyword !== "") {
      filteredTransactions = mokupTransactions.filter((transaction) => {
        return (
          transaction.patientId.toLowerCase().includes(searchKeyword.toLowerCase()) ||
          transaction.patientPhoneNumber.toLowerCase().includes(searchKeyword.toLowerCase())
        );
      });
    }
    if (dateRange !== "") {
      const today = new Date();
      filteredTransactions = filteredTransactions.filter(transaction => {
        const transactionDate = new Date(transaction.dateTime);
        switch (dateRange) {
          case "1":
            return transactionDate.getDate() === today.getDate() &&
              transactionDate.getMonth() === today.getMonth() &&
              transactionDate.getFullYear() === today.getFullYear();
          case "2":
            return today.getTime() - transactionDate.getTime() <= 7 * ONEDAY_IN_MILLISECONDS;
          case "3":
            return today.getTime() - transactionDate.getTime() <= 30 * ONEDAY_IN_MILLISECONDS;
          case "4":
            return today.getTime() - transactionDate.getTime() <= 90 * ONEDAY_IN_MILLISECONDS;
          default:
            return true;
        }
      })
    }
    setTransactions(filteredTransactions);
  }, [searchKeyword, dateRange]);

  return (
    <div className="container-fluid">
      <h5 className="my-3">Danh sách đơn hàng</h5>
      <div className="row">
        <div className="col-12">
          <div className="card border-light bg-white shadow-sm">
            <div className="card-body">
              <div className="row mb-2">
                <div className="col-lg-8">
                  <form onSubmit={handleSubmitForm} className="row gy-2 gx-2 align-items-center justify-content-between justify-content-lg-start">
                    <div className="col-12 col-sm-auto">
                      <div className="input-group mb-3">
                        <span className="input-group-text">
                          <i className="bi bi-search"></i>
                        </span>
                        <input onBlur={handleSearch} type="text" className="form-control bg-white" id="search" placeholder="Tìm kiếm" />
                      </div>
                    </div>
                    <div className="col-12 col-sm-auto">
                      <div className="input-group mb-3">
                        <span className="input-group-text">
                          <i className="bi bi-filter"></i>
                        </span>
                        <select onChange={handleFillter} className="form-select bg-white" id="dateRange">
                          <option value="">Lọc theo ngày</option>
                          <option value="1">Hôm nay</option>
                          <option value="2">Hôm qua</option>
                          <option value="3">7 ngày qua</option>
                          <option value="4">1 tháng qua</option>
                          <option value="5">3 tháng qua</option>
                        </select>
                      </div>
                    </div>
                  </form>
                </div>
                <div className="col-lg-4">
                  <div className="text-end">
                    <Link to="/admin/edit" className="btn btn-primary mb-3 me-2">
                      <i className="bi bi-plus-circle me-2"></i>
                      Tạo đơn hàng
                    </Link>
                    <button
                      type="button"
                      className="btn btn-outline-secondary mb-3"
                      onClick={handleExportExcel}
                    >Xuất Excel</button>
                  </div>
                </div>
              </div>
              <div className="table-responsive mb-4">
                <table className="table table-centered table-nowrap mb-0">
                  <thead className="table-light">
                    <tr>
                      <th>Mã Giao Dịch</th>
                      <th>Mã Bệnh Nhân</th>
                      <th>Số Điện Thoại</th>
                      <th>Trạng Thái</th>
                      <th>Thời Gian Giao Dịch</th>
                      <th>Số Tiền Thanh Toán</th>
                      <th>Mã Ngân Hàng</th>
                    </tr>
                  </thead>
                  <tbody>
                    {transactions.map((transaction, id) => (
                      <tr key={id}>
                        <td className="text-body fw-bold">{transaction.id}</td>
                        <td  className="text-body fw-bold">{transaction.patientId}</td>
                        <td>{transaction.patientPhoneNumber}</td>
                        <td>
                          <span className={`badge rounded-pill text-bg-${transaction.isPaid ? "success" : "danger"}`}>
                            {transaction.isPaid ? "Đã thanh toán" : "Chưa thanh toán"}
                          </span>
                        </td>
                        <td>{transaction.dateTime}</td>
                        <td>{transaction.total}</td>
                        <td>{transaction.bankCode}</td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Admin;
