import { useState, useMemo, useEffect } from "react";
import { useParams } from "react-router-dom";
import { saveAs } from "file-saver";
import transactions from "../mockup/transaction.json"

export default function Home() {
  const params = useParams();
  const transactionId = params.transactionId;
  const [listBank, setListBank] = useState([]);

  useEffect(() => {
    fetch("https://api.vietqr.io/v2/android-app-deeplinks")
      .then(res => res.json())
      .then(data => {
        setListBank(data.apps);
      })
      .catch(err => {
        console.log(err);
      })
  }, []);

  const currentTransaction = useMemo(() => {
    return transactions.find(transaction => transaction.id === transactionId);
  }, [transactionId]);

  const paymentInfo = useMemo(() => {
    return currentTransaction.paymentInfo;
  }, [currentTransaction]);

  const bankInfo = useMemo(() => {
    return listBank.find(bank => bank.appId === paymentInfo.paymentAppId);
  }, [listBank, paymentInfo]);

  const handleCopyText = (text, event) => {
    navigator.clipboard.writeText(text);
    event.target.innerText = "Đã sao chép";
    event.target.classList.remove("bg-success-subtle");
    event.target.classList.remove("text-success");
    event.target.classList.add("bg-danger-subtle");
    event.target.classList.add("text-danger-emphasis");
  };

  const handleDownloadImg = (imgUrl, imgName) => {
    saveAs(imgUrl, imgName);
  };

  return (
    <>
      <div className="row">
        <div className="d-none d-lg-block col-8">
          <div className="bg-white rounded p-3 shadow mb-3">
            <h4 className="text-center mb-4">Chi tiết thanh toán</h4>
            <div className="row">
              <div className="col-6 mb-3">
                <p className="m-0 text-secondary fw-bold fs-7">Mã Giao Dịch</p>
                <p className="m-0 fw-bold">{currentTransaction.id}</p>
              </div>
              <div className="col-6 mb-3">
                <p className="m-0 text-secondary fw-bold fs-7">Mã Bệnh Nhân</p>
                <p className="m-0 fw-bold">{currentTransaction.patientId}</p>
              </div>
              <div className="col-6 mb-3">
                <p className="m-0 text-secondary fw-bold fs-7">Số Điện Thoại</p>
                <p className="m-0 fw-bold">{currentTransaction.patientPhoneNumber}</p>
              </div>
              <div className="col-6 mb-3">
                <p className="m-0 text-secondary fw-bold fs-7">Trạng Thái</p>
                <p className="m-0 fw-bold">{currentTransaction.status}</p>
              </div>
              <div className="col-6 mb-3">
                <p className="m-0 text-secondary fw-bold fs-7">Thời Gian Giao Dịch</p>
                <p className="m-0 fw-bold">{currentTransaction.dateTime}</p>
              </div>
              <div className="col-6 mb-3">
                <p className="m-0 text-secondary fw-bold fs-7">Số Tiền Thanh Toán</p>
                <p className="m-0 fw-bold">{currentTransaction.total}</p>
              </div>
            </div>
          </div>
        </div>
        <div className="col-lg-4">
          <div className="bg-white rounded p-3 shadow mb-3">
            <h4 className="text-center mb-4">Chuyển khoản ngân hàng</h4>
            <p className="m-0 fs-7">Bạn cần sử dụng một App Ngân hàng bất kỳ để chuyển khoản chính xác nội dung bên dưới.</p>
            <p className="m-0 fs-7">Chuyển khoản sai thông tin hoặc sai số tiền có thể mất nhiều ngày để xử lý.</p>
            <ul className="list-group list-group-flush">
              <li className="list-group-item px-0">
                {/* <div className="m-3">
                  <img
                    className="w-100"
                    src={currentTransaction.qrcode}
                    alt={currentTransaction.id}
                  />
                </div> */}
                <div className="mb-3 row justify-content-between align-items-center">
                  <div
                    onClick={() => handleDownloadImg(currentTransaction.qrcode, currentTransaction.id)}
                    className="col-3 d-flex flex-column align-items-center"
                  >
                    <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" fill="currentColor" className="bi bi-cloud-download" viewBox="0 0 16 16">
                      <path d="M4.406 1.342A5.53 5.53 0 0 1 8 0c2.69 0 4.923 2 5.166 4.579C14.758 4.804 16 6.137 16 7.773 16 9.569 14.502 11 12.687 11H10a.5.5 0 0 1 0-1h2.688C13.979 10 15 8.988 15 7.773c0-1.216-1.02-2.228-2.313-2.228h-.5v-.5C12.188 2.825 10.328 1 8 1a4.53 4.53 0 0 0-2.941 1.1c-.757.652-1.153 1.438-1.153 2.055v.448l-.445.049C2.064 4.805 1 5.952 1 7.318 1 8.785 2.23 10 3.781 10H6a.5.5 0 0 1 0 1H3.781C1.708 11 0 9.366 0 7.318c0-1.763 1.266-3.223 2.942-3.593.143-.863.698-1.723 1.464-2.383z"/>
                      <path d="M7.646 15.854a.5.5 0 0 0 .708 0l3-3a.5.5 0 0 0-.708-.708L8.5 14.293V5.5a.5.5 0 0 0-1 0v8.793l-2.146-2.147a.5.5 0 0 0-.708.708l3 3z"/>
                    </svg>
                    <span className="fs-7 fw-semibold">Tải về</span>
                  </div>
                  <img
                    src={currentTransaction.qrcode}
                    alt={currentTransaction.id}
                    className="col-6"
                  />
                  <div className="col-3 d-flex flex-column align-items-center">
                    <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" fill="currentColor" className="bi bi-upload" viewBox="0 0 16 16">
                      <path d="M.5 9.9a.5.5 0 0 1 .5.5v2.5a1 1 0 0 0 1 1h12a1 1 0 0 0 1-1v-2.5a.5.5 0 0 1 1 0v2.5a2 2 0 0 1-2 2H2a2 2 0 0 1-2-2v-2.5a.5.5 0 0 1 .5-.5z"/>
                      <path d="M7.646 1.146a.5.5 0 0 1 .708 0l3 3a.5.5 0 0 1-.708.708L8.5 2.707V11.5a.5.5 0 0 1-1 0V2.707L5.354 4.854a.5.5 0 1 1-.708-.708l3-3z"/>
                    </svg>
                    <span className="fs-7 fw-semibold">Chia sẻ</span>
                  </div>
                </div>
                <div className="d-flex align-items-center">
                  <img
                    width={50}
                    src={bankInfo?.appLogo}
                    alt=""
                    className="me-2"
                  />
                  <div>
                    <p className="m-0 text-secondary fw-bold fs-7">Ngân hàng</p>
                    <p className="m-0 fw-bold">{bankInfo?.bankName}</p>
                  </div>
                </div>
              </li>
              <li className="list-group-item px-0 d-flex justify-content-between align-items-center">
                <div>
                  <p className="m-0 text-secondary fw-bold fs-7">Số tài khoản</p>
                  <p className="m-0 fw-bold">{paymentInfo.bankAccountNumber}</p>
                </div>
                <div>
                  <button
                    type="button"
                    className="btn btn-sm bg-success-subtle text-success fw-medium fs-7"
                    onClick={(event) => handleCopyText(paymentInfo.bankAccountNumber, event)}
                  >Sao chép</button>
                </div>
              </li>
              <li className="list-group-item px-0 d-flex justify-content-between align-items-center">
                <div>
                  <p className="m-0 text-secondary fw-bold fs-7">Tên tài khoản</p>
                  <p className="m-0 fw-bold">{paymentInfo.bankAccountName}</p>
                </div>
                <div>
                  <button
                    type="button"
                    className="btn btn-sm bg-success-subtle text-success fw-medium fs-7"
                    onClick={(event) => handleCopyText(paymentInfo.bankAccountName, event)}
                  >Sao chép</button>
                </div>
              </li>
              <li className="list-group-item px-0 d-flex justify-content-between align-items-center">
                <div>
                  <p className="m-0 text-secondary fw-bold fs-7">Số tiền</p>
                  <p className="m-0 fw-bold">{paymentInfo.paymentAmount}</p>
                </div>
                <div>
                  <button
                    type="button"
                    className="btn btn-sm bg-success-subtle text-success fw-medium fs-7"
                    onClick={(event) => handleCopyText(paymentInfo.paymentAmount, event)}
                  >Sao chép</button>
                </div>
              </li>
              <li className="list-group-item px-0 d-flex justify-content-between align-items-center">
                <div>
                  <p className="m-0 text-secondary fw-bold fs-7">Nội dung chuyển khoản</p>
                  <p className="m-0 fw-bold">{paymentInfo.paymentContent}</p>
                </div>
                <div>
                  <button
                    type="button"
                    className="btn btn-sm bg-success-subtle text-success fw-medium fs-7"
                    onClick={(event) => handleCopyText(paymentInfo.paymentContent, event)}
                  >Sao chép</button>
                </div>
              </li>
            </ul>
          </div>
        </div>
      </div>
      <div className="d-block d-lg-none bg-white rounded p-3 shadow">
        <h4 className="text-center mb-4">Chọn ngân hàng thanh toán</h4>
        <div className="row">
          {listBank.length && listBank.map((bank, id) => (
            <div key={id} className="col-3 col-sm-2 mb-3">
              <a
                href={bank.deeplink}
                target="_blank"
                rel="noreferrer"
              >
                <img
                  width={'100%'}
                  src={bank.appLogo}
                  alt={bank.appName}
                  className="rounded shadow"
                />
              </a>
            </div>
          ))}
        </div>
      </div>
    </>
  )
}
