import { useState } from "react";
import { Link } from "react-router-dom";
import mokupTransactions from "../mockup/transaction.json"

export default function Home() {
  const [transactions, setTransactions] = useState([]);

  const handleSubmitForm = (event) => {
    event.preventDefault();
    const listTransactions = mokupTransactions.filter(transaction =>
      transaction.patientId === event.target.patientId.value &&
      transaction.patientPhoneNumber === event.target.phoneNumber.value
    )
    setTransactions(listTransactions);
  };

  return (
    <>
      <div className="bg-light rounded p-3 shadow mb-3">
        <h2 className="mb-3 text-center">Tra cứu thông tin thanh toán</h2>
        <p>Vui lòng nhập mã bệnh nhân và số điện thoại!</p>
        <form onSubmit={handleSubmitForm} className="row">
          <div className="mb-3 col-md-6">
            <label htmlFor="patientId" className="form-label">Mã bệnh nhân</label>
            <input
              type="text"
              className="form-control"
              id="patientId"
              required
              pattern="^[A-Z0-9]+$"
              title="Input must contain only uppercase letters."
            />
          </div>
          <div className="mb-3 col-md-6">
            <label htmlFor="phoneNumber" className="form-label">Số điện thoại</label>
            <input type="number" className="form-control" id="phoneNumber" required/>
          </div>
          <div>
            <button type="submit" className="btn btn-primary">Tìm kiếm</button>
          </div>
        </form>
      </div>
      <div className="bg-light rounded p-3 shadow">
        {transactions.length === 0 ? (
          <p>Bạn không có giao dịch nào!</p>
        ) : (
          <>
            <h2 className="text-center">Thông tin thanh toán</h2>
            <div className="table-responsive mt-4">
              <table className="table table-striped align-middle">
                <thead>
                  <tr>
                    <th scope="col">Mã Giao Dịch</th>
                    <th scope="col">Mã Bệnh Nhân</th>
                    <th scope="col">Số Điện Thoại</th>
                    <th scope="col">Trạng Thái</th>
                    <th scope="col">Thời Gian Giao Dịch</th>
                    <th scope="col">Số Tiền Thanh Toán</th>
                    <th scope="col">QR code</th>
                    <th scope="col"></th>
                  </tr>
                </thead>
                <tbody>
                  {transactions.map((transaction, id) => (
                    <tr key={id}>
                      <td>{transaction.id}</td>
                      <td>{transaction.patientId}</td>
                      <td>{transaction.patientPhoneNumber}</td>
                      <td>{transaction.status}</td>
                      <td>{transaction.dateTime}</td>
                      <td>{transaction.total}</td>
                      <td>
                        <img src={transaction.qrcode} alt={transaction.patientId} width={'100px'}/>
                      </td>
                      <td>
                        <Link to={`payment/${transaction.id}`}>Thanh toán</Link>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          </>
        )}
      </div>
    </>
  );
};
